'use client';

import React from 'react';
import { useClient } from './ClientContext';

interface Thread {
  thread_id: string;
  created_at: string;
  messages: Array<{
    role: string;
    content: string;
  }>;
}

interface ThreadContextType {
  threads: Thread[];
  currentThreadId: string | null;
  isLoading: boolean;
  loadThreads: () => Promise<void>;
  createNewThread: () => Promise<string>;
  loadThreadHistory: (threadId: string) => Promise<void>;
  deleteThread: (threadId: string) => Promise<void>;
  setCurrentThreadId: (threadId: string | null) => void;
}

const ThreadContext = React.createContext<ThreadContextType | null>(null);

export function ThreadProvider({ children }: { children: React.ReactNode }) {
  const client = useClient();
  const [threads, setThreads] = React.useState<Thread[]>([]);
  const [currentThreadId, setCurrentThreadId] = React.useState<string | null>(null);
  const [isLoading, setIsLoading] = React.useState(false);

  const loadThreads = React.useCallback(async () => {
    if (!client) return;
    
    try {
      setIsLoading(true);
      const response = await client.threads.search();
      const formattedThreads = response.map(thread => ({
        thread_id: thread.thread_id,
        created_at: thread.created_at || new Date().toISOString(),
        messages: []
      }));
      setThreads(formattedThreads);
      
      // Load messages for each thread
      await Promise.all(formattedThreads.map(thread => 
        loadThreadHistory(thread.thread_id)
      ));
    } catch (error) {
      console.error('Error loading threads:', error);
    } finally {
      setIsLoading(false);
    }
  }, [client]);

  const createNewThread = React.useCallback(async () => {
    if (!client) throw new Error('Client not initialized');

    try {
      setIsLoading(true);
      const response = await client.threads.create();
      const newThread: Thread = {
        thread_id: response.thread_id,
        created_at: new Date().toISOString(),
        messages: []
      };
      
      setThreads(prev => [...prev, newThread]);
      setCurrentThreadId(newThread.thread_id);
      return newThread.thread_id;
    } catch (error) {
      console.error('Error creating thread:', error);
      throw error;
    } finally {
      setIsLoading(false);
    }
  }, [client]);

  const loadThreadHistory = React.useCallback(async (threadId: string) => {
    if (!client) return;

    try {
      const state = await client.threads.getState(threadId);
      if (state.messages && state.messages.length > 0) {
        setThreads(prev => prev.map(thread => {
          if (thread.thread_id === threadId) {
            return {
              ...thread,
              messages: state.messages
            };
          }
          return thread;
        }));
      }
    } catch (error) {
      console.error(`Error loading thread history for ${threadId}:`, error);
    }
  }, [client]);

  const deleteThread = React.useCallback(async (threadId: string) => {
    if (!client) return;

    try {
      await client.threads.delete(threadId);
      setThreads(prev => prev.filter(t => t.thread_id !== threadId));
      if (currentThreadId === threadId) {
        setCurrentThreadId(null);
      }
    } catch (error) {
      console.error('Error deleting thread:', error);
      throw error;
    }
  }, [client, currentThreadId]);

  React.useEffect(() => {
    if (client) {
      loadThreads();
    }
  }, [client]);

  const value = React.useMemo(() => ({
    threads,
    currentThreadId,
    isLoading,
    loadThreads,
    createNewThread,
    loadThreadHistory,
    deleteThread,
    setCurrentThreadId
  }), [threads, currentThreadId, isLoading, loadThreads, createNewThread, loadThreadHistory, deleteThread]);

  return (
    <ThreadContext.Provider value={value}>
      {children}
    </ThreadContext.Provider>
  );
}

export function useThread() {
  const context = React.useContext(ThreadContext);
  if (!context) {
    throw new Error('useThread must be used within a ThreadProvider');
  }
  return context;
}
